body {
  margin: 1rem;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar .navbar-toggle .icon-bar {
  background-color: #000000; /* Changes regular toggle color */
}

.navbar .navbar-toggle {
	float: left;
	margin-left: 5 px;
}

.navbar .navbar-brand {
	float: right;
	margin-right: 5 px;
	font-style: italic;
}

.navbar {
	width: 100%;
}
.active {
    text-decoration: underline;
}

.header {
	font-size:18px;
	width:100%;
	text-align: center;
}

.header span {
	padding: 0px 30px;
	text-align: center;
	margin: 0 auto;
}


@media only screen and (max-width: 600px) {
	.header #first {
		margin-left:5%;
	}

	.header #second {
		margin-left:20%;
	}

	.header #third {
		margin-left:35%;
	}
	.header span {
		display: block;
		text-align: left;
	}
}
.leftbox {
	float:right;
}

.row .leftbox {
	border-bottom: 1px solid #D3D3D3;
}

.row .leftbox + .row .leftbox {
   border-top:0;
}

.bar-left {
	height: 3px;
	background-color: #D3D3D3;
	margin-bottom: 20px;
	margin-top: 20px;
}

.bar-right {
	height: 3px;
	background-color: #555555;
	margin-bottom: 20px;
	margin-top: 20px;
}

.row {
	margin-left: 1%;
	margin-right: 1%;
}

.form-checkbox {
	float: right;
}

.issue-label {
	float: left;
	width: 99%;
}
.issue-selector {
	display:block;
}
.all-issues {
	padding-top:10px;
}
.footer {
	float: right;
	font-size: 18px;
	text-decoration: none;
	margin-right: 5px;
}
.footer a {
	color: #000000;
}
.container {
	padding:10px;
	display:block;
}
.plan {
	padding-top: 20px;
	text-align: center;
}
.form-group {
/*	font-size:10px;
*/	display: inline;
	width: 100%;
}
#vote-method {
	margin:5px;
}
#when {
	margin:5px;
}
#bda {
	margin:5px;
}
#buddy {
	width: 100%;
}
.form-control {
	width: 100%;
	text-align: center;
}
ul {
	list-style: none;
}
.p1 {
	position: relative;
	z-index: 10;
}
.canvas-container {
    margin-right: 15px;
}
.container {
	text-align: left;
	width: 100%;
	padding:40px;
}
